								<h3>Our approach for traffic optimization</h3>
								<p>Large traffic congestion is a serious civilizational and commercial issue causing wasting time, energy and large pollution. Thus it is important to manage it properly, but vehicular traffic in cities is a complex process, so its a difficult task. In our approach, in the TensorTraffic project, we tackle the so-called Traffic Signal Setting problem in which we try to find optimal configurations of signals for a given traffic conditions. The signal configuration may consist of offsets (times (in seconds) from a certain point in time to the first switch from the red signal state to the green signal state), durations of red and green signal phases or a more complex strategies for traffic signal adaptation.</p>
