								<p>The problem of finding optimal traffic signal settings for a given traffic conditions was proven to be NP-hard even for relatively simple traffic simulation models, so in our approach we use metaheuristics, such as genetic algorithms, for finding (sub)optimal settings. Settings of traffic signals can be evaluated using traffic simulations, so we are also developing Traffic Simulation Framework (TSF) for running microscopic simulations on realistic road networks. Currently, the tool is calibrated to run simulations on maps of Warsaw originating from the OpenStreetMap project, with different settings of parameters and initial conditions.</p>
