							<p>
                TSF does quite a good job in evaluating different traffic signal control strategies, it can read different traffic signal settings provided as an input, run simulations and compute values of the performance function measuring quality of traffic control, e.g., the total time of drive or the total time of waiting on a red signal. However, accurate, microscopic traffic simulations are time consuming (evaluating 10 minutes of traffic may take many seconds), while many optimization algorithms trying to find best settings require running large number of simulations. At the same time, we often don’t need information about the whole simulation process (as we can obtain from by running Traffic Simulation Framework), we only need information about a function measuring quality of given traffic signal settings. We can’t compute these values precisely without running simulation, but for the optimization tasks, approximate values, with a relatively small error, should be sufficient. 
							</p>
<p>
Therefore, we decided to build metamodels approximating outcomes of traffic simulations. Initial metamodels, based on simplifying simulation model, didn’t give sufficient accuracy (relative error between simulation outcomes and metamodel outcomes was at the level of 20%). The probable reason is that they didn’t take into consideration microscopic interactions between cars, e.g., close to the crossroads and while waiting on a red signal. Another approach was to build metamodels based on machine learning algorithms such as neural networks or gradient tree boosting models (there is a theoretical justification that neural networks may be especially good for the task - the Universal Approximation Theorem [3]).
</p>
<p>
For that purpose, we generated using TSF a large set consisting of 117 033 evaluations of different traffic signal settings (offsets of signals) and divided it into training, validation and test sets, which were later used to train different machine learning algorithms, mostly neural networks. Each simulation was set to simulate 10 minutes of traffic on a region of Warsaw consisting of 15 crossroads with traffic signals (Stara Ochota district). This time, the approximations of traffic simulation outcomes turn out to be very good, giving the average relative error of approximations at the level of 1-2% and maximal error at the level 7%. In general, the approximations seems to be quite good, which can be also shown in the Figure 2 presenting distribution of a relative error of approximation on a (randomly selected) test se consisting of 14 634 elements). Later, we achieved similar results using metamodels based on LightGBM [4].
</p>

<p>
Metamodels based on machine learning algorithms after training give very good accuracy of approximating outcomes of microscopic traffic simulations, but they are also very fast. Evaluating a single setting can be done in time lower than 0.1 seconds, which is a few orders of magnitude faster than in case of an accurate evaluation using TSF. Therefore, such metamodels can be applied to test different traffic optimization algorithms, e.g., genetic algorithms, finding (sub)optimal traffic signal settings for a given traffic conditions. The presented approach was described in details in our scientific papers published at NIPS 2016 [5] and MT-ITS 2017 [6] and is under development in the TensorTraffic project.
</p>
